@import url(https://fonts.googleapis.com/css?family=Lato|Open+Sans);
.overall {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
}

.breadcrumb {
    margin-top: 24px;
    margin-left: 27.8px;
    list-style: none;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.breadcrumb .list-item {
    display: inline;
    font-size: 24px;
}

.breadcrumb .list-item+.list-item:before {
    padding: 8px;
    content: "/\A0";
}

.list-item:last-child {
    color: #000;
}
.page-content {
    margin-top: 45px;
    margin-left: 24px;
    display: grid;
    grid-template-columns: repeat(4, 150px);
    grid-template-rows: 120px auto 120px;
    grid-column-gap: 20px;
    grid-row-gap: 65px;
    -webkit-align-items: stretch;
            align-items: stretch;
}

.grid-item {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.icon {
    margin: 12px 22px 9px 23px;
    padding-top: 12px;
    padding-bottom: 12px;
}

.icon-blue {
    margin: 12px 22px 9px 23px;
    padding-right: 12px;
    padding-left: 12px;
    padding-top: 12px;
    background: #E6F5FF;
    padding-bottom: 12px;
    border-radius: 8px;
}

.icon:hover {
    cursor: pointer;
}

.file-name {
    font-family: "Lato", -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 16px;
    color: #535B62;
    letter-spacing: 0px;
    margin-top: -10px;
}
.searchbar-input:focus {
    outline: none;
}

.searchbar-input::-webkit-input-placeholder {
    font-size: 15px;
    margin-bottom: 3px;
    opacity: 0.6;
}

.searchbar-input::-ms-input-placeholder {
    font-size: 15px;
    margin-bottom: 3px;
    opacity: 0.6;
}

.searchbar-input::placeholder {
    font-size: 15px;
    margin-bottom: 3px;
    opacity: 0.6;
}
.banner {
    width: 70%;
    background: #f44336;
    color: #fff;
    display: grid;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 90px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 22px;
    font-weight: 500;
    margin-top: 30px;
    margin-bottom: 30px;
    padding: 20px;
}

.query {
    color: #ffeb3b;
    font-style: italic; 
}
.popup {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
}

.popup_inner {
    background: #ffffff;
    border: 1px solid rgba(221, 224, 228, 0.7);
    box-shadow: 0 16px 64px 0 rgba(0, 0, 0, 0.08);
    border-radius: 8px;
    position: absolute;
    left: 25%;
    right: 25%;
    top: 25%;
    bottom: 25%;
    margin: auto;
    height: 447px;
    width: 316px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.top {
    clear: both;
}

.heading {
    float: left;
    font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
        "Lucida Sans", Arial, sans-serif;
    font-size: 18px;
    margin-top: 25px;
    color: #2f363f;
    position: absolute;
    margin-left: 91px;  /* Design specs have it as 111px and that is way off the center. */
}

button::-moz-focus-inner {
    border: 0;
}

.closeButton {
    background: transparent;
    font-size: 22px;
    border: none;
    height: 24px;
    width: 24px;
    padding: 2px;
    float: right;
    margin-top: 22px;
    position: relative;
    margin-left: 268px;
    margin-right: 24px;
    outline: none;
    cursor: pointer;
}

.fileType {
    margin-top: 16px;
    margin-bottom: 32px;
    border: 1px solid #DDE0E4;
    border-radius: 8px;
    padding: 0;
    width: 128px;
    height: 34px;
    display: -webkit-inline-flex;
    display: inline-flex;
    cursor: pointer;
}

.file_button {
    cursor: pointer;
    font-size: 14px;
    width: 63px;
    height: 32px;
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
    border:none;
    outline: none;
}

.folder_button {
    cursor: pointer;
    font-size: 14px;
    width: 65px;
    height: 32px;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    border: none;
    outline: none;
}

.selected {
    background: #4AB7FF;
    color: #fff;
}

.texts {
    margin-left: 48px;
}

.createBtn {
    margin-bottom: 48px;
    background: #4AB7FF;
    border-radius: 8px;
    color: #fff;
    height: 40px;
    width: 220px;
    font-size: 14px;
    font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
        "Lucida Sans", Arial, sans-serif;
    border: none;
    outline: none;
}

.createBtn:hover {
    cursor: pointer;
}

.input {
    background: #FFFFFF;
    border: 1px solid #DDE0E4;
    border-radius: 8px;
    font-size: 14px;
    color: #AFB2B6;
    padding-left: 16px;
    width: 220px;
    height: 40px;
    margin-bottom: 16px;
}

.input :nth-child(4) {
    margin-bottom: 24px;
}
.popup2 {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
}

.popup2_inner {
    background: #ffffff;
    border: 1px solid rgba(221, 224, 228, 0.7);
    box-shadow: 0 16px 64px 0 rgba(0, 0, 0, 0.08);
    border-radius: 8px;
    position: absolute;
    left: 25%;
    right: 25%;
    top: 25%;
    bottom: 25%;
    margin: auto;
    height: 395px;
    width: 316px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.top2 {
    clear: both;
}

.file_icon {
    margin-top: 44px;
    margin-bottom: 36px;
}

.heading2 {
    float: left;
    margin-top:25px;
    margin-right: 78px;
    font-size: 18px;
    color: #2F363F;
    margin-left: 80px;
}

.button2 {
    color: #000;
    border-radius: 8px;
    text-align: center;
    padding: 4px;
    outline: none;
    background: transparent;
    border: 2px dashed #000000;
}

.closeButton2 {
    background: transparent;
    font-size: 18px;
    border: none;
    height: 24px;
    width: 24px;
    float: right;
    margin-top: 24px;
    position: absolute;
}

.descriptions {
    display: grid;
    grid-template-columns: 115px 115px;
    grid-template-rows: 24px 24px 24px 24px; 
    grid-column-gap: 8px;
    grid-row-gap: 24px;
}

.id_value {
    font-size: 16px;
    color: #81878C;
}

.ids {
    margin-bottom: 24px;
    justify-self: center; 
}

.identifier {
    text-align: right;
    font-size: 16px;
    color: #2F363F;
}
.menu {
    height: 145px;
    width: 159px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 16px;
    color: #535B62;
    letter-spacing: 0;
    background: #FFFFFF;
    border: 1px solid rgba(221, 224, 228, 0.50);
    box-shadow: 0 12px 24px 0 rgba(0, 0, 0, 0.05);
    border-radius: 8px;
    z-index:1000;
    margin-left: 200px;
}

.menu-text {
    padding-top: 15px;
    padding-left: 24px;
}

.option {
    background: #fff;
    /* height: 43px; */
    height: 48px;   /* Corrected for the gap that was present according to the design specs. */
    width: 100%;
}

.option:hover {
    cursor: pointer;
    background: #EEEFF1;
}

.option:nth-child(3) {
    color: #FF5942;
}
.content {
    float: right;
    width: calc(100% - 256px);
    background: #fff;
    height: 100vh;
}
.font-size-12 {
    font-family: "Lato", sans-serif;
    font-size: 24px;
    color: #AFB2B6;
    font-size: 12px;
    letter-spacing: 0;
    font-weight: bold;
    padding-left: 32px;
    padding-top: 32px;
    padding-bottom: 15px;
}

.sidebar {
    width: 256px;
    float: left;
    background: #F9FAFC;
    height: 100vh;
}

.sidebar-menu {
    width: 100%;
}

.sidebar-item {
    height: 43px;
    font-size: 16px;
    color: #2F363F;
    font-family: 'Lato', sans-serif;
    clear: both;
}

.inner-item {
    color: #535B62;
    clear: both;
}

.sidebar-item:active {
    background-color: #EEEFF1;
}

.sidebar-item-text {
    float: left;
    padding-left: 32px;
    padding-top: 12px;
}

.sidebar-item-inner-text {
    font-size: 16px;
    font-family: inherit;
    padding-left: 16px;
    padding-top: 12px;
    float: left;
}

.sidebar-item-extend {
    float: right;
    background-color: inherit;
    border: none;
    border-radius: 50%;
    width: 24px;
    height: 24px;
    margin-top: 9px;
    margin-right: 24px;
}

.vert-line {
    width: 2px;
    height: 16px;
    margin-top: 15px;
    margin-left: 32px;
    background: #EEEFF1;
    float: left;
}

.sidebar-item-extend:hover {
    cursor: pointer;
}

.sidebar-item-extend:focus {
    outline: 0;
}

.sidebar-item-extend .clicked {    /* TODO: Doesn't work as intended */
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    -webkit-transition-delay: 500ms;
            transition-delay: 500ms;
}
/* Also have to use Helvetica Neue */
.mainContent {
    width: 100%;
    position: relative;
}
